html, body {
  font-family: ProximaNova, sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 14px;
  line-height: 1.5em;
  overflow-x: hidden;
  position: relative;
  min-width: 320px;
  min-height: 5000px;
}

* {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

a,
a:focus,
a:hover {
  outline: none;
  text-decoration: none;
}

input[type="text"], input[type="tel"] {
  border: 1px solid;
  border-radius: 30px;
  line-height: 1em;
  padding: 20px 30px;
  box-shadow: none;
  font-size: 14px;
  width: 100%;
  max-width: 300px;
  background: transparent;
  outline: none;
  -webkit-appearance: none;
  -moz-appearance: none;
}

input[type="text"]:required:valid, input[type="tel"]:required:valid {
  outline: none;
  box-shadow: none;
  -webkit-appearance: none;
  font-size: 14px;
}

.header {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  z-index: 1000;
  background: transparent;
  padding: 25px 0;
  transition: all .3s linear;
}

.header__wrapper {
  display: flex;
}

@media only screen and (max-width: 992px) {
  .header__wrapper {
    justify-content: center;
    z-index: 1200;
  }
}

@media only screen and (max-width: 576px) {
  .header__wrapper {
    justify-content: flex-start;
  }
}

.header.scroll {
  position: fixed;
  background: rgba(3, 14, 43, 0.99);
  padding: 10px 0;
  transition: all .6s ease;
}

.header.scroll .nav {
  opacity: 1;
}

.header.active {
  background: rgba(3, 14, 43, 0.99);
  padding: 25px 0;
}

.logo {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 215px;
  margin-right: 85px;
  z-index: 1100;
}

@media only screen and (max-width: 1200px) {
  .logo {
    margin-right: 0;
  }
}

.logo img {
  width: 41px;
  height: auto;
}

.logo__text {
  display: flex;
  flex-direction: column;
}

.logo span {
  display: inline-block;
  line-height: 1em;
  font-family: Gilroy, sans-serif;
}

.logo span.name {
  color: #fff;
  font-weight: 800;
  font-size: 29px;
  letter-spacing: 0.1em;
  line-height: 1em;
}

.logo span.description {
  color: #ccc;
  font-size: 12px;
}

.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

@media only screen and (max-width: 992px) {
  .nav {
    flex-direction: column;
    position: absolute;
    left: 0;
    right: 0;
    top: 91px;
    bottom: 0;
    height: calc( 100vh - 91px);
    transform: translateX(100vw);
    background: rgba(3, 14, 43, 0.99);
    justify-content: flex-start;
    transition: all .5s ease;
    overflow: auto;
    opacity: 0;
  }
  .nav.active {
    transform: translateX(0);
    transition: all .8s ease;
    overflow: auto;
    padding: 25px 0;
    opacity: 1;
  }
}

.nav__button {
  z-index: 1100;
  position: absolute;
  background: transparent;
  width: 40px;
  height: 40px;
  display: none;
  right: 5%;
  top: 50%;
  transform: translateY(-50%);
}

@media only screen and (max-width: 992px) {
  .nav__button {
    display: block;
  }
}

.nav__button.active .bar {
  background: transparent;
}

.nav__button.active .bar::before {
  left: 0;
  top: 0;
  transform: rotate(135deg);
}

.nav__button.active .bar::after {
  right: 0;
  top: 0;
  transform: rotate(220deg);
}

.nav__button .bar {
  position: absolute;
  width: 40px;
  height: 4px;
  background: #fff;
  top: 50%;
  transform: translateY(-50%);
  transition: all .3s ease;
}

.nav__button .bar::before, .nav__button .bar::after {
  content: '';
  position: absolute;
  width: 40px;
  height: 4px;
  left: 0;
  background: #fff;
  transition: all .3s ease;
}

.nav__button .bar::before {
  top: 10px;
}

.nav__button .bar::after {
  top: -10px;
}

